import moment from 'moment';
import React from "react";
import { Link } from "react-router-dom";
import { UNIVERSAL_DATE_FORMAT } from "../helper/Constant";
import { getKey } from '../helper/Helper';

export class SimpleTable extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            // DATA STATE
            templateType: [{'label': 'vj'}],
            selectedTemplateType: [],
            templateChangedForContent: null,
            templateSelectedForContent: 0,
        }
    }

    /**
       * Renders Button on Typehead.
       * @returns Button element
       */
    renderToggleButton = ({ isMenuShown, onClick }) => (
        <button
            type="button"
            style={{ position: "absolute", height: "100%", top: "0px", right: "0px", border: "1px solid lightgray" }}
            onClick={(e) => {
                e.preventDefault();
                onClick(e);
            }}
        ><span className="fa fa-angle-down"></span></button>
    );

    onChangeTemplateId = (e, contentId) => {
        // this.props.setTemplateId(e.target.value);
        this.setState({ templateSelectedForContent: e.target.value })
        this.setState({ templateChangedForContent: contentId });
        const applyTemplateToContent = this.props.mappingOfContentTemplate.map(el => {
            el.contentId === contentId && (el.templateId = e.target.value)
            return el;
        });
        this.props.setContentTemplate(applyTemplateToContent);
    }

    onApplyToAll = () => {
        document.getElementsByName('modelId').forEach((el) => { el.value = `${this.state.templateSelectedForContent}` })
        const applyTemplateToAllContent = this.props.mappingOfContentTemplate.map(el => ({ ...el, templateId: this.state.templateSelectedForContent.toString() }));
        this.props.setContentTemplate(applyTemplateToAllContent);
    }

    render() {
        let parseSelectedContent = [];
        if (typeof this.props.selectedContent === 'string') {
            parseSelectedContent = JSON.parse(this.props.selectedContent);
        } else {
            parseSelectedContent = this.props.selectedContent;
        }

        return (
            <>
                <table className="table table-bordered table-datatable table-hover table-striped Contents__table-element">
                    <thead>
                        <tr  className={this.props.decideToShowQuery() && 'disabled-table'}>
                            {Object.keys(contentAttribute).map((item, idx) => <th key={idx}>{contentAttribute[item]}</th>)}
                        </tr>
                    </thead>
                    <tbody>
                        {
                            parseSelectedContent.length > 0 && (
                                parseSelectedContent.map(item => {
                                    return (
                                        <tr key={item.id}>
                                            <td>{item[getKey(item)]}</td>
                                            {/* {fetchTitleOrName(item)} */}
                                            <td>{`${item.createdBy.firstname} ${item.createdBy.lastname}`}</td>
                                            <td>{moment(new Date(item.updatedAt)).format(UNIVERSAL_DATE_FORMAT)}</td>
                                            <td>{moment(new Date(item.publishedAt)).format(UNIVERSAL_DATE_FORMAT)}</td>
                                            <td width="30%">
                                                <select defaultValue={'none'} style={{ display: "inline-block", width: "50%" }} name="modelId" className="form-control" onChange={(e) => this.onChangeTemplateId(e, item.id)}>
                                                    <option value="none" disabled hidden>Select Template</option>
                                                    {/* <option value="1">Select Template </option> */}
                                                    {this.props.templateList.map((el) => {
                                                        return <option key={el.id} value={el.id}>{el.templateName}</option>;
                                                    })}
                                                </select>
                                                {item.id === this.state.templateChangedForContent && <div onClick={this.onApplyToAll} style={{ display: "inline-block", width: "50%", textAlign: "center" }}>
                                                    <Link to="#">
                                                        <span className="fa fa-copy" style={{ margin: "0px 10px" }}></span>
                                                        Apply to all contents
                                                    </Link>
                                                </div>}
                                            </td>
                                        </tr>
                                    )
                                })
                            )
                        }
                    </tbody>
                </table>
            </>
        );

    }
}


const contentAttribute = {
    Title: "Name",
    createdAt: "Created by",
    updatedAt: "Last edited",
    createDate: "Created date",
    selectDefaultTemplate: "Select default template *"
    // restirctions: "Restrictions"
}