import {
    Button, Col, Row
} from 'patternfly-react';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { getCollectionTypes, getTemplate } from '../api/Api';
import { BTN_ADD_NEW_CONTENT } from '../helper/Constant';
import MockModalManager from '../ui/MockModalManager';
import { SimpleTable } from '../ui/SimpleTable';

const strapiDomain = `${process.env.REACT_APP_STRAPI_API_TARGET_URL}`;

export default class Config extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // selectedContent: [],
            // name: null,
            // nameTwo: null,
            collectionTypes: [],
            // selectedContentId: 0,
            // selectedTemplateId: 'default',
            templateList: []
        };
        this.handleAddNewContent = this.handleAddNewContent.bind(this);
    }

    setSelectedContent = (selectedContentArr, selectedContenttype) => {
        // this.setState({ selectedContent: selectedContentArr })
        // this.setState({ name: selectedContentArr })
        // this.setState({ nameTwo: encodeURIComponent(JSON.stringify(selectedContentArr)) })
        // this.setState({ selectedContentId: selectedContentArr[0].id })
    }

    handleTypeaheadChangeContentType = selected => {
        let selectedContentType = selected.map(option => option.label);
        this.setState({ templateList: selectedContentType });
    };

    componentDidMount = async () => {
        let contentTypes = await getCollectionTypes();
        contentTypes = contentTypes.data.filter(obj => {
            return obj && (obj.uid && obj.uid.startsWith("api::")) && obj.isDisplayed;
        });
        const contentTypeRefine = [];
        contentTypes.length && contentTypes.forEach(element => {
            contentTypeRefine.push({ label: element.info.pluralName })
        });
        let { data: templatesList } = await getTemplate(this.props.selectedContentName ? this.props.selectedContentName : '');
        this.setState({ templateList: templatesList, collectionTypes: templatesList })
    }

     /**
     * Navigate to Strapi dashboard/login page in a new tab on browser
     */
      handleAddNewContent = () => {
        let strapiContentManagerUrl = `${strapiDomain}/admin/content-manager`;
        var newWindow = window.open(strapiContentManagerUrl, '_blank');
        if(newWindow) {
            newWindow.focus();
        }
    }

    decideToShowQuery = () => {
        return (
            this.props.selectedContentLabel && !this.props.selectedContent.length
        )
    }

    onChangeTemplateId = (e) => {
        this.props.SaveQueryHandler(
            {
                "templateId": e.target.value,
                "searchTerm": this.props.searchTerm
            }
        );
    }

    render() {
        return (
            <form className="form-horizontal SingleContentConfigForm well">
                <Row>
                    <Col xs={12}>
                        <div>
                            <div>
                                <span className="icon fa fa-puzzle-piece" title="Widget" />
                                <h5 className="SingleContentConfigFormBody__widgetTitle">Content List</h5>
                                <div className="SectionTitle SectionTitle__non-collapsable" role="button">
                                    <span>Info</span>
                                </div>
                                <div className="row">
                                    <Col xs={6}>
                                        <h3 className="SingleContentConfigFormBody__contentTitle">
                                            Contents :-
                                        </h3>
                                    </Col>
                                    <Col xs={6} className="SingleContentConfigFormBody__addButtons">
                                        <Link to="/configpage">
                                            <Button bsStyle="primary">
                                                {this.props.selectedContent.length || this.decideToShowQuery() ? 'Edit' : 'Add existing'} {!this.decideToShowQuery() && this.props.selectedContent.length > 0 && ' List of '} {this.decideToShowQuery() ? 'query' : 'content'}
                                            </Button>
                                        </Link>
                                        <Button className="AddContentTypeFormBody__save--btn" bsStyle="primary" onClick={this.handleAddNewContent}>
                                            {BTN_ADD_NEW_CONTENT}
                                        </Button>
                                    </Col>
                                </div>
                                {/* Not in use MockModalManager */}
                                {/* <MockModalManager rightSide={false} setSelectedContent={this.setSelectedContent} /> */}
                                <div style={{ marginTop: "1rem" }}></div>
                                <SimpleTable
                                    setTemplateId={this.props.setTemplateId}
                                    templateList={this.state.templateList}
                                    // content={this.state.selectedContent}
                                    selectedContent={this.props.selectedContent}
                                    mappingOfContentTemplate={this.props.mappingOfContentTemplate}
                                    setContentTemplate={this.props.setContentTemplate}
                                    decideToShowQuery={this.decideToShowQuery}
                                />
                                {
                                     (
                                        <div className="form-horizontal SingleContentConfigForm custom-well well">
                                            <Row className="divider">
                                                <Col xs={2}>
                                                    <span className="SectionTitle" role="button">
                                                        <span>Query</span>
                                                    </span>
                                                </Col>
                                                <Col xs={10}>
                                                    <div className="SingleContentConfigFormBody__addButtons">
                                                        <span><b>Select default template*</b></span>
                                                    </div>
                                                </Col>
                                            </Row>
                                            {/* <hr /> */}
                                            {this.decideToShowQuery() && <Row>
                                                <Col xs={2} style={{ borderRight: "1px solid lightgrey" }}>
                                                    Query Filter
                                                </Col>
                                                <Col xs={1} style={{ borderRight: "1px solid lightgrey" }}>
                                                </Col>
                                                <Col lg={6} style={{ border: "1px solid lightgrey", padding: ".2rem" }}>
                                                    <code>
                                                        <span style={{ color: "#0088ce" }}><b>Collection Type =  </b> {this.props.selectedContentLabel} </span>
                                                        <span style={{ color: "#0088ce" }}>
                                                            { this.props.searchTerm && <b>WHERE Name =  </b>}
                                                            { this.props.searchTerm && `%${this.props.searchTerm}% ` }
                                                            {/* <b>WHERE Name =  </b> %{this.props.searchTerm}% */}
                                                             <b>ORDER BY</b> creation date <b>DESC</b></span>
                                                    </code>
                                                </Col>
                                                <Col lg={3}>
                                                <div className="SingleContentConfigFormBody__addButtons">
                                                <select defaultValue={'none'} style={{ display: "inline-block" }} name="modelId" className="form-control" onChange={(e)=>this.onChangeTemplateId(e)}>
                                                    <option value="none" disabled hidden>Select Template</option>
                                                    {/* <option value="1">Select Template </option> */}
                                                    {this.state.templateList.map((el) => {
                                                        return <option key={el.id} value={el.id}>{el.templateName}</option>;
                                                    })}
                                                </select>
                                                </div>
                                                </Col>
                                            </Row>}
                                        </div>
                                    )
                                }
                            </div>
                        </div>
                    </Col>

                </Row>
            </form>
        )
    }
}
